.muski-sequencer-matrix th {
  font-size: 0.85rem;
  font-weight: normal;
}
.muski-sequencer-matrix td {
  width: 40px;
  height: 40px;
  border: 2px solid white;
  background-color: transparent;
}
.muski-sequencer-matrix .muski-sequencer-cell {
  display: block;
  width: 40px;
  height: 40px;
  background-color: #dcdcdc;
  border: none;
}
.muski-sequencer-matrix .muski-sequencer-cell.active {
  outline: 3px solid #ffff4b;
  background-color: #efef9c;
}
.muski-sequencer-matrix .muski-sequencer-cell.on {
  background-color: #61dcbd;
}
.muski-sequencer-matrix .muski-sequencer-cell.active.on {
  outline: 3px solid #ffff4b;
  background-color: #61dc7e;
}
.muski-sequencer-matrix .muski-sequencer-row-label {
  padding-right: 1em;
  text-align: right;
}
.muski-sequencer-matrix .muski-sequencer-col-label {
  text-align: center;
}